import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/index'
import index from '@/pages/index/index'//
import Baojie from '@/pages/baojie/baojie'// 
import Rchang from '@/pages/baojie/rchang'// 

Vue.use(Router)

let router= new Router({
  // mode:'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index,
      children:[
        {path:'',component:index,meta: {title:''}}
      ]
    },
    {
      path:'/baojie',
      name:'baojie',
      component:Baojie,
      meta:{
        title:''
      }
    },
    {
      path:'/rchang',
      name:'rchang',
      component:Rchang,
      meta:{
        title:''
      }
    },
   
  ]
})

////
// to  from next() 
router.beforeEach((to, from, next) => {
  //title
  // console.log(to.matched[0].name)
  if(to.matched[0].name !== "index") {
    // common.checkRegisterStatus()
  }

  changeTitle(to.meta.title);
  next();
});

//title
function changeTitle(title) {
  title = title ? title : '';
  window.document.title = title;
};


export default router



